header{
    display: grid;
    grid-template-columns: 2fr 1fr;
    background-color: black;
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
    text-decoration: none;
}
a{
    text-decoration: none;
}

nav{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: end;
    align-items: center;
}

.logo{
    color: white;
    text-decoration: none;
    cursor: pointer;
}

.nav_item{
    color: #001AFF;
    font-size: 25px;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    padding-left: 25px;
    background-color: black;
}

.nav_item:hover{
    color: white;
}

.cart_btn{
    display:grid;
    grid-template-columns: 1fr 2fr;
}
.count_cart_items {
    font-size: 20px;
    color:#001AFF;
    background-color: white;
    border-radius: 5px;
    padding:5px;
    width: fit-content;
}